/* About Section CSS */
.about-section {
  color: white;
  background-color: black;
  padding: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  opacity: 0; /* Start invisible */
  transform: translateY(20px); /* Start slightly off-screen */
  transition: opacity 0.8s ease-in-out, transform 0.8s ease-in-out; /* Smooth transition for both opacity and position */
}

.about-section.visible {
  opacity: 1; /* Fade in */
  transform: translateY(0); /* Move to its final position */
}

.about-header h1 {
  font-size: 4rem;
  margin-bottom: 20px;
}

.about-content {
  max-width: 800px;
  font-size: 1.5rem;
  line-height: 1.6;
}

.name {
  font-weight: bold;
  font-size: 1.7rem;
}

.pronouns {
  font-size: 1.2rem;
  color: gray;
}

.about-footer {
  display: flex;
  align-items: center;
  margin-top: 50px;
  justify-content: center;
}

.company-logo {
  width: 50px;
  height: 50px;
  border-radius: 8px ;
  margin-right: 20px;
}

.company-info .position {
  font-weight: bold;
}

.company-info .company {
  color: gray;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .about-header h1 {
    font-size: 3rem;
  }

  .about-content {
    font-size: 1.2rem;
  }

  .about-footer {
    flex-direction: column;
    text-align: center;
  }

  .company-logo {
    margin-bottom: 10px;
  }
}

@media (max-width: 480px) {
  .about-header h1 {
    font-size: 2.5rem;
  }

  .about-content {
    font-size: 1rem;
  }

  .company-logo {
    width: 40px;
    height: 40px;
  }
}
